.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.quiz-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  background-color: #2c2727;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.quiz-container h1 {
  text-align: center;
}

.question {
  margin-bottom: 20px;
}

.question p {
  margin-bottom: 10px;
}

.options {
  padding-left: 20px;
}

.options label {
  display: block;
  margin-bottom: 5px;
}

.options input {
  margin-right: 5px;
}

.submit-button {
  display: block;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
}

